@import '../Common/Stiles/sass/variables.scss';
@import '../Common/Stiles/sass/mixins.scss';

.mainBlock {
  height: 100vh;
  background: url('https://i0.wp.com/digital-photography-school.com/wp-content/uploads/2014/06/how-to-see-black-and-white-06.jpg?resize=600%2C400&ssl=1') ;
  background-attachment: fixed;
  background-repeat: no-repeat;
  background-position: top;
  background-size: cover;
  background-color: rgba(0,0,0,0.75);
  background-blend-mode: overlay;

  .particles {
    position: relative;
    top: 0;
    left: 0;
    button: 0;
    right: 0;
    opacity: 0.3;

  }

  .container {
    flex-wrap: wrap;
    padding: 0 40px;
    height: 100%;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;


    & h3 {
      @include text($textFontFamily, $textColor2, 21px, 400);
      margin-bottom: 8px;
    }

    & h1 {
      @include text($textFontFamily, $primaryColor, 48px, 800);
      margin: 0;
      text-align: center;
    }

    div{
      margin-top: 8px;
      span{
        @include text($textFontFamily, $textColor2, 17px, 400);
      }
    }


    .ava {
      width: 300px;
      height: 300px;
      background-color: #728732;
    }
  }

}



